<template>
    <section>
        <div class="theme-primary partition-full">
            <span class="p-name text-white">Zone Management</span>
            <div class="clear showclear"></div>
            <div class="p-actions legends_topbar float-right">
                <div class="p-action-map collapse_map">
                    <a href="javascript:;" class="e-c-map p-action-btn text-white align-items-center d-flex " v-on:click="ShowHideMap"><i id="icnToggleMap" class="fa fa-angle-double-up pr-2"></i><span id="spnToggleMap">{{mapHeaderText}}</span></a>
                </div>
                <div class="p-action-map">
                    <a href="javascript:;" v-if="isShowCreatePolygon" @click="CreatePolygon" class="p-action-btn text-white align-items-center d-flex"><i class="fa fa-map-marker pr-2"></i><span>Create Polygon</span></a>
                </div>
            </div>
        </div>
        <ManageMap class="row" mapType="Google" :editZone="editZone" mapWidth="100%" mapHeight="780px" v-if="isShowMap" @AddZonePopUp="AddZonePopUp" mapContainer="zoneMapDiv" @FetchData="FetchData" @GetAddress="GetAddress" :latLng="latLng" :Data="mapObj" :key="componentKey" :config="config" :zoneId="zoneId"></ManageMap>
        <tg-list v-show="IsZoneView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="" IdentityColumn="ZONE_ID"
            :LegendArray="[]" v-on:ActionButtonClick="actionButtonClick"
            :RenderRowActionMethod="renderActions" :widgets="[]" :listType="['List']" :ListData="ZoneList" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <a class="inactive-disabled">
                        <span v-bind:class="getStatusClassList(data.row.STATUS_ID, 'status_id')"
                            v-if="data.row.STATUS_ID == 'Active'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                        <span>
                            {{ $options.filters.formatDate(data.row.CREATED_AT) }}
                        </span>
                    </template>
            </template>
        </tg-list>
       <modalSaveZoneVue v-if="editZone" :zoneEditData="zoneEditData" @Close="Close" @SaveZoneData="SaveZoneData"></modalSaveZoneVue>
    </section>
</template>
<script>
import Schema from "@/views/Zone/ZoneSchema";
import ManageMap from './../../components/Common/MapComponent.vue';
import AddZone from "../../components/Modal/modalAddZone.vue"
import DataServices from "@/services/DataService";
import modalSaveZoneVue from '../../components/Modal/modalSaveZone.vue';
export default {
    components:{
        ManageMap,
        AddZone,
        modalSaveZoneVue
    },
    data() {
        return {
            Headers: [{
                "COLUMN_NAME": "ZONE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "NUMBER_OF_SHAPE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Number of Shapes",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ZONE_TYPE",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Zone Type",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "CREATED_BY",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created by",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created at",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "status",
                "DISPLAY_ORDER": 4
            }
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            isShowCreatePolygon:false,
            IsZoneView: true,
            listActions: Schema.listActions,
            ZoneList: [],
            UserListLevelOne: [],
            UserListLevelTwo: [],
            alllevelselectedusers: [],
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            NoRecord: true,
            TotalRecords: 0,
            SearchCondition: '',
            SearchTypeCondition: '',
            SearchConditionByName: '',
            SearchType: '',
            MapCoordArray: [],
            txtZoneName: '',
            txtZoneDesc: '',
            PolygonType: "Polygon",
            ZoneType: "Locator",
            isSending: false,
            userid: [],
            GeoJsonFile: '',
            GeoJsonData: '',
            ViewZoneById: '',
            ItemSelected: null,
            ZoneId: 0,
            inputs: [],
            YardTrueFalse: false,
            selectoptions: '',
            SelectedItem: null,
            IsSearchTrueOrFalse: false,
            ShowMapData: false,
            ShowCreatePolygonText: false,
            ISFSMLOCATING: false,
            StrokeWidthValue: "2",
            StrokeOpacityValue: "0.0",
            FillOpacityValue: "0.5",
            UserDataListing: [],
            searchzonetype: '',
            search: '',
            isShowMap:false,
            componentKey: 0,
            zoneId:0,
            mapHeaderText: 'Expand map',
            mapObj:[],
            showAddZone:false,
            zoneData:[],
            editZone:false,
            zoneEditData:[],
            config:{
                isShowSearchBox:true,
                isShowCurrentLocation:true
            }

        }
    },
    created: async function () {
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchData();
        await Schema.ShowSearchData();
        this.ShowHideMap();
    },

    methods: {
        ShowHideMap: function () {     
            debugger   
            this.mapObj=[];   
            var icon = $('#icnToggleMap');
            if (this.isShowMap == false) {
                this.isShowMap = true;
                this.isShowCreatePolygon=true;
                this.mapHeaderText = 'Collapse map';
                if (icon.hasClass('fa-angle-double-up')) {
                    icon.removeClass('fa-angle-double-up');
                    icon.addClass('fa-angle-double-down');
                }
            }
            else {
                this.isShowMap = false;
                this.mapHeaderText = 'Expand map';
                if (icon.hasClass('fa-angle-double-down')) {
                   

                    icon.removeClass('fa-angle-double-down');
                    icon.addClass('fa-angle-double-up');
                }
            }
        },
        Close:function(){
            var vm=this;
            vm.showAddZone=false;
            vm.editZone=false;
        },
        AddZonePopUp:function(){
            var vm=this;
            vm.mapObj=[];
            vm.showAddZone=true;
        },
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            debugger
            switch (callbackfunction) {
                case 'View':
                    this.openPopupForSetupZoneList(item)
                    break;
                case 'Edit':
                    this.Edits(item)
                    break;
            }
        },
        openPopupForSetupZoneList:function(item){
            debugger
            var vm=this;
            //vm.isShowMap=true;
            vm.zoneData=item;
            var params=`id=${item.ZONE_ID}`;
            vm.zoneId=item.ZONE_ID;
            vm.mapObj=[];
            var obj = {
                    latlong:"",
                    refValue:  vm.zoneId,
                    popupHeaderHtml: '',
                    popupHeaderColor: "",
                    popupBodyHtml: '',
                    popupFooterHtml: '',
                    pinLatLong: {
                        latitude: "",
                        longitude: ""
                    },
                    polygons: [],
                    address: "",
                    priority:'',
                    color_code:''
                }  
            let PolygonArray=[];
            DataServices.GetZoneById(params).then(function (response) {
                let plygonLatLng=JSON.parse(response.data.result.DATA[0].JSON_DATA).features;
                plygonLatLng.forEach(function(row,i){
                    let latlng = [];
                    row.geometry.coordinates[0].forEach(function(items,it){
                        latlng.push({
                            latitude:items[0],
                            longitude:items[1]
                        })
                    })  
                    PolygonArray.push({
                        name: "",
                        fillColor: row.properties["fillColor"],
                        strokeColor:row.properties["stroke"],
                        strokeWeight: row.properties["stroke-width"],
                        strokeOpacity: row.properties["stroke-opacity"],
                        opacity: row.properties["fill-opacity"],
                        latLong: latlng,
                        FormName:vm.zoneData.ZONE_NAME,
                        Description:vm.zoneData.ZONE_DESCRIPTION,
                        ZoneType:vm.zoneData.ZONE_TYPE,
                        YardEmails:vm.zoneData.YARD_EMAILS,
                        locatorids:row.properties["locatorids"]
                    })
                })         
                obj.polygons = PolygonArray;
                vm.mapObj.push(obj);
                if(vm.isShowMap==false){
                    vm.ShowHideMap();
                    //vm.isShowCreatePolygon=false;
                }
                else{
                   // vm.isShowCreatePolygon=false;
                }
                $(window).scrollTop(0);
                //vm.componentKey+=1;
            })
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {
            debugger;
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else {
                this.search = '';
            }
            if (params.hasOwnProperty('searchzonetype')) {
                this.searchzonetype = params['searchzonetype'];
            }
            else {
                this.searchzonetype = '';
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: async function () {
            var vueObj = this;
            var url = `pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}`;
            if (!this.isBlank(this.search)) {
                url += `&search=${this.search}`;
            }
            if (!this.isBlank(this.searchzonetype)) {
                url += `&searchzonetype=${this.searchzonetype}`;
            }
            await DataServices.ZoneList(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "STATUS_ID") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == 'CREATED_AT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data.result.DATA.length > 0) {
                        vueObj.ZoneList = response.data.result.DATA;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.listheaderbuttons[2].isdisabled=true;
                        vueObj.CurrentPage = vueObj.PageNumber;
                        if (vueObj.ZoneList.length > 0) {
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                            vueObj.NoRecord = false;
                        } else {
                            vueObj.NoRecord = true;
                        }
                        $.each(vueObj.ZoneList.DATA, function (key, val) {
                            var obj = {
                                Coord: val.LAT_LONG_MAP,
                                Shape_Color: val.SHAPE_COLOR,
                                GeoJSONData: val.ZONE_COORDINATES_DATA == null ? null : JSON.parse(val.ZONE_COORDINATES_DATA)
                            };
                            vueObj.MapCoordArray.push(obj);
                        });
                        var item = vueObj.ZoneList;
                        if (item != null) {
                            vueObj.ZoneId = item.ZONE_ID;
                            vueObj.txtZoneName = item.ZONE_NAME;
                            vueObj.txtZoneDesc = item.ZONE_DESCRIPTION;
                            vueObj.ItemSelected = item;
                        }
                        vueObj.isShowMap=false;
                    }
                else {
                    vueObj.noRecord = true;
                    vueObj.ZoneList = [];
                    vueObj.TotalRecords = 0
                }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.ZoneList = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }              
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        commonHeaderButtonClick: function (event) {
            debugger
            switch (event.callbackfunction) {
                case "openaddTicketGroup":
                    this.openaddTicketGroup();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openupdateStatus":
                    this.showUpdateStatusModal = true;
                    break;
                case "openaddZone":
                    this.openaddZone();
                    break;
            }
        },
        openaddZone:function(){
            var vm=this;
            vm.mapObj=[];
            vm.isShowCreatePolygon=true;
            vm.isShowMap=true;
        },
        Edits:function(data){
            debugger
          var vm=this;
          vm.zoneEditData=[];
          vm.zoneEditData.push({
                ZONENAME:data.ZONE_NAME,
                ZONEDESCRIPTION:data.ZONE_DESCRIPTION,
                Refrence:'Edit',
                ZONETYPE:data.ZONE_TYPE,
                YARDEMAILS:data.YARD_EMAILS
          });
          vm.editZone=true;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (sortBy) {
            debugger
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        SaveZoneData:function(obj){
            debugger
            var vm=this;
            var postString = {
                    ZoneId: obj.ZoneId,
                    ZoneName: obj.Name,
                    ZoneDesc: obj.Description,
                    ZoneType: obj.ZoneType,
                    YardEmails:obj.YardEmails
                };
            var objectPostString = JSON.stringify(postString);
            DataServices.UpdateZone(objectPostString).then(function (response) {
                if (response.data.code == "1") {
                    //vm.ShowAlert(vm.$t('Zone')+vm.$t('UpdatedSuccess'), "success", true);
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Zone")]), "success", true, vm.$t("Alert"));
                    vm.editZone=false;
                    vm.FetchData();
                }
            })
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonDeleteData: function (event) {
            debugger;
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticketing Service Board")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "FSM",
                        subModuleCode: "FSM_DETAIL"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Zone')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Zone')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        CreatePolygon:function(){
            var vm=this;
            vm.config={
                isShowDrawButton:true,
                isShowSearchBox:true,
                isShowCurrentLocation:true
            }
            //vm.componentKey+=1;
        }
    }
}
</script>