import DataServices from "@/services/DataService";
let buttons =
    [
        
    {
        id: 'Import',
        title: 'Import',
        iconClass: 'fa fa-download',
        callbackfunction: '',
        isdisabled: true,
        isvisible: false,
    },
    {
        title: 'Add Zone',
        iconClass: 'fa fa-plus',        
        callbackfunction: 'openaddZone',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: true,
        id:'AddZone',
         
    },
   
    {
        id: 'DeleteMultiple',
        title: 'Delete',
        iconClass: 'fa-trash-o',
        isEnabledOnSelectedRow: true,
        callbackfunction: 'commonDeleteData',
        isdisabled: true,
        isvisible: true,
        isEnabledOnSelectedRow: true
    }
   
    ];
    let leftsearchSchema = [
        {
            fieldName: 'Zone Name',
            fieldType: "text",
            fieldIcon: "fa fa-map-marker",
            fieldSearchConditionName: ['search'],
            paramName:"search",
            isSearch: false,
            value: "",
            listOptions: []
        },
        {
            fieldName: 'Zone Type',
            fieldType: "dropdown",
            fieldIcon: "fa fa-map-marker",
            fieldSearchConditionName: ['searchzonetype'],
            paramName:"searchzonetype",
            isSearch: false,
            value: "",
            listOptions: [{
                value:'',name:'Zone Type',
            },
            {
                value:'high_profile',name:'High_Profile'
            },
            {
                value:'Locator',name:'Locator'
            },
            {
                value:'LocatorAfterHours' ,name:'Locator After Hours'
            },
            {
                value:'Yard',name:'Yard'
            }
        ]
        
        },
    ]

    
    let listActions=[  
        {
            title:'View',   
            href: 'javascript:void(0)',         
            icon: 'fa fa-eye action-icon',
            callbackfunction:'View',
            additionalSpan:''
        },
        {
            title:'Edit',   
            href: 'javascript:void(0)',         
            icon: 'fa fa-pencil action-icon',
            callbackfunction:'Edit',
            additionalSpan:''
        }
    ];
    
    async function ShowSearchData() {
        var vm = this;
        var url = `&moduleName=${'FSM'}&type=${'TICKET_RESOLVER'}`;
        await DataServices.GetDDLData(url).then(function (response) {
                if (response.data.status == "Success") {
                    vm.selectoptions = response.data.result.DATA;
                    response.data.result.DATA.forEach(element => {
                        let obj = {
                            name: element.name,
                            value: element.value
                        }
                        vm.leftsearchSchema[1].listOptions.push(obj);
                    });
                }
            });
  
};
    export default { buttons, leftsearchSchema,listActions,ShowSearchData};